@use '../utils/variables' as *;

.section {
    padding: 20px 0;

    @media screen and (min-width: $bp-desktop) {
            padding: 48px 0;
        }

    &__title {
        font-size: 32px;
        font-weight: 900;
        line-height: 1.5;

        @media screen and (min-width: $bp-desktop) {
            font-size: 64px;
            line-height: 1.3;
        }

        &--centered {
            text-align: center;
        }
    }
}

